export const pageQuery = graphql`
  query {
    allMarkdownRemark(limit: 2, sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
          node {
            fields {
              slug
            }
            frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
                description
            }
          }
      }
    }
  }
`
